___CSS_LOADER_EXPORT___.push([module.id, `
#setting-button {
    position: fixed;
    bottom: 20px;
    left: 20px;
    color: white;
    z-index: 1000;
    background-color: transparent;
    border: none;
    transition: transform 0.3s;
    box-sizing: border-box;
}

#setting-button:hover {
    transform: rotate(90deg);
    cursor: pointer;
}

footer {
    display: flex;
    justify-content: center;
    align-items: center;
}

#footer-text {
    font-size: 1rem;
    transition: color 1.5s;
}

#setting-icon {
    font-size: 4rem;
    transition: color 1.5s;
}

#setting-section {
    width: 80%;
    height: 80%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: grid;
    grid-template-rows: 1fr 5fr;
    z-index: -1000;
    background-color: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(10px);
    border: 5px solid white;
    transition: border-color 1.5s;
}

#setting-header {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 3px solid white;
    margin: 0 20px;
    transition: border-color 1.5s;
}

#setting-title {
    font-size: 4rem;
}

#difficulty-title,
#color-title,
#obstacle-title {
    font-size: 3rem;
    transition: color 1.5s;
}

#setting-content {
    display: grid;
    grid-template-rows: 2fr 3fr;
    gap: 20px;
    padding: 20px;
}

#difficulty-section header,
#color-section header,
#obstacle-section header {
    display: flex;
    align-items: center;
}

#difficulty-section {
    display: grid;
    grid-template-rows: 1fr 5fr;
    border-bottom: 3px solid white;
    transition: border-color 1.5s;
}

#difficulty-option {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.difficulty-button {
    width: 20%;
    min-width: fit-content;
    background-color: transparent;
    border: 5px solid white;
    padding: 20px 10px;
    color: white;
    font-size: 2rem;
    transition: background-color 0.3s, color 0.3s;
}

#easy-button:hover, .easy-enabled {
    background-color: rgba(0, 255, 72, 0.8);
    color: black;
    cursor: pointer;
}

#medium-button:hover, .medium-enabled {
    background-color: rgba(255, 255, 0, 0.8);
    color: black;
    cursor: pointer;
}

#hard-button:hover, .hard-enabled {
    background-color: rgba(255, 0, 0, 0.8);
    color: black;
    cursor: pointer;
}

#other-section {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

#color-section,
#obstacle-section {
    display: grid;
    grid-template-rows: 1fr 5fr;
}

#color-section {
    border-right: 3px solid white;
    transition: border-color 1.5s;
}

#color-section header,
#obstacle-section header {
    display: flex;
    align-items: center;
    justify-content: center;
}

#color-option-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.color-button {
    width: 50%;
    background-color: transparent;
    border: 5px solid white;
    padding: 20px 10px;
    color: white;
    font-size: 2rem;
    transition: background-color 0.3s, color 0.3s;
}

#color-option-container #red-option:hover, .red-enabled {
    background-color: #9d0208;
    color: black;
    cursor: pointer;
}

#color-option-container #green-option:hover, .green-enabled {
    background-color: #008000;
    color: black;
    cursor: pointer;
}

#color-option-container #blue-option:hover, .blue-enabled {
    background-color: #0077b6;
    color: black;
    cursor: pointer;
}

#obstacle-option-container {
    display: grid;
    grid-template-rows: 3fr 2fr;
}

#obstacle-presence-section {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.obstacle-button {
    width: 30%;
    background-color: transparent;
    border: 5px solid white;
    padding: 20px 10px;
    color: white;
    font-size: 2rem;
    transition: background-color 0.3s, color 0.3s;
}

#obstacle-presence-section #on-button:hover, .on-enabled {
    background-color: rgba(0, 255, 72, 0.8);
    color: black;
    cursor: pointer;
}

#obstacle-presence-section #off-button:hover, .off-enabled {
    background-color: rgba(255, 0, 0, 0.8);
    color: black;
    cursor: pointer;
}

#obstacle-quantity-section {
    position: relative;
    width: 80%;
    margin: auto;
}

#obstacle-quantity-section div {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#obstacle-quantity-section label, #obstacle-quantity-section span {
    font-size: 2rem;
    padding-bottom: 10px;
}

#obstacle-quantity-input {
    width: 100%;
}

.active {
    background-color: white;
    color: black;
}

#instructions-section {
    position: fixed;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(50px);
    z-index: -1;
}   

#instructions-container {
    border: 5px solid white;
    padding: 20px 10px;
}

#instructions-section header {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 3px solid white;
    margin: 0 20px;
}

#instructions-title {
    font-size: 4rem;
    padding-bottom: 10px;
}

#instructions-content p {
    font-size: 2rem;
    padding: 20px;
}

#game-over-section {
    position: fixed;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: black;
    z-index: -1;
}

#game-over-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#winner-text {
    font-size: 4rem;
    padding: 20px;
}

#restart-button {
    width: fit-content;
    background-color: transparent;
    border: 5px solid white;
    padding: 20px 10px;
    color: white;
    font-size: 2rem;
    transition: background-color 0.3s, color 0.3s;
}

#restart-button:hover {
    background-color: rgba(189, 189, 189, 0.8);
    color: black;
    cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/styles/setting.css"],"names":[],"mappings":";AACA;IACI,eAAe;IACf,YAAY;IACZ,UAAU;IACV,YAAY;IACZ,aAAa;IACb,6BAA6B;IAC7B,YAAY;IACZ,0BAA0B;IAC1B,sBAAsB;AAC1B;;AAEA;IACI,wBAAwB;IACxB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,UAAU;IACV,WAAW;IACX,eAAe;IACf,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,aAAa;IACb,2BAA2B;IAC3B,cAAc;IACd,oCAAoC;IACpC,2BAA2B;IAC3B,uBAAuB;IACvB,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,8BAA8B;IAC9B,cAAc;IACd,6BAA6B;AACjC;;AAEA;IACI,eAAe;AACnB;;AAEA;;;IAGI,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,2BAA2B;IAC3B,SAAS;IACT,aAAa;AACjB;;AAEA;;;IAGI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,2BAA2B;IAC3B,8BAA8B;IAC9B,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,sBAAsB;IACtB,6BAA6B;IAC7B,uBAAuB;IACvB,kBAAkB;IAClB,YAAY;IACZ,eAAe;IACf,6CAA6C;AACjD;;AAEA;IACI,uCAAuC;IACvC,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,wCAAwC;IACxC,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,sCAAsC;IACtC,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;;IAEI,aAAa;IACb,2BAA2B;AAC/B;;AAEA;IACI,6BAA6B;IAC7B,6BAA6B;AACjC;;AAEA;;IAEI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,6BAA6B;IAC7B,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,6BAA6B;IAC7B,uBAAuB;IACvB,kBAAkB;IAClB,YAAY;IACZ,eAAe;IACf,6CAA6C;AACjD;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,UAAU;IACV,6BAA6B;IAC7B,uBAAuB;IACvB,kBAAkB;IAClB,YAAY;IACZ,eAAe;IACf,6CAA6C;AACjD;;AAEA;IACI,uCAAuC;IACvC,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,sCAAsC;IACtC,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,oBAAoB;AACxB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,uBAAuB;IACvB